/* eslint-disable */
import Article from "@/components/article/Article.vue";

export default {
    name: 'Home',
    components: {
        Article
    },
    data: () => ({
        timer: null
    }),
    props: {
        scrolled: Number,
        bgHeight: Number
    },
    computed: {
        posts: function () {
            return this.$store.state.home.postsList.data;
        },
        pagination: function () {
            const { pageCount, pageSize, total } = this.$store.state.home.postsList;
            return { pageCount, pageSize, total };
        },
        page: function () {
            return this.$store.state.home.page;
        },
        format: function () {
            return this.$store.state.global.hexoConfig.dateTimeFormat.date_format;
        }
    },
    fetch: async function ({ store }) {
        const prePage = store.state.home.page;
        // avoid double fetch initial data
        if (prePage !== 1) {
            await store.dispatch('home/fetchHomePostsList', { page: 1 }); 
        }
    },
    mounted: async function () {
        let isLoading = false;
        let nowPage = this.page,
            pageCount = this.pagination.pageCount;
        
        window.onscroll = async () => {
            let bottomOfWindow = document.documentElement.offsetHeight - document.documentElement.scrollTop - window.innerHeight <= 200;
            if (bottomOfWindow && isLoading == false && nowPage < pageCount) {
                nowPage++;
                isLoading = true;
                await this.$store.dispatch('home/fetchHomePostsList', { page: nowPage });
                isLoading = false
            }
        };
        /*if (this.page < this.pageCount) {
            window.addEventListener('scroll', this.onPage(this.page), true);
        } else {
            window.addEventListener('scroll', this.onPage(this.page), true);
        }*/
    },
    created() {
        // window.addEventListener('scroll', this.onPage(this.page));
    },
    methods: {
        onPage: async function(page) {
            let isLoading = false;
            let bottomOfWindow = document.documentElement.offsetHeight - document.documentElement.scrollTop - window.innerHeight <= 200;

            page++;

            if (bottomOfWindow && isLoading == false && this.page < this.pageCount) {
                isLoading = true;
                await this.$store.dispatch('home/fetchHomePostsList', { page: page });
                isLoading = false
            }        
        }
    }
}